.rs-form__control-group {
  font-family: SourceHanSansCN-Normal;
  font-size: 14px;
  margin-bottom: 20px;
  color: #626262; }
  .rs-form__control-group.has-error .rs-form__control-label {
    color: #f25252; }
  .rs-form__control-group.has-error .rs-input {
    border: 1px solid #f44;
    color: #f44; }
  .rs-form__control-group .rs-form__control-label {
    display: inline-block;
    font-size: 14px;
    line-height: 30px;
    text-align: right;
    vertical-align: top;
    width: 120px; }
  .rs-form__control-group .rs-form__controls {
    margin-left: 10px;
    display: inline-block;
    vertical-align: top;
    word-break: break-all;
    min-height: 32px;
    line-height: 32px; }



